import { Injectable } from '@angular/core';
import {GlobalService} from '../service/global.service';
import { HttpClient,HttpHeaders} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})


export class UserService {
  headers:any;
  constructor(private http:HttpClient ,private _globalservice:GlobalService) { 

  }

  

  getOTP(customerPhone: number, customerName: string){
    var body ={
      customerPhone,
      customerName
    }
    const headers = { 'Content-Type': 'application/json', 'encodedkey': this._globalservice.encodedKey }

  return  this.http.post(this._globalservice.idineUrl+'/GetOTP'
  ,body,
  {headers}
  )
}
  verifyOTP(cust_phone,OTP){
   

    const headers = { 'Content-Type': 'application/json',  }
    return  this.http.get<any>(`${this._globalservice.idineUrl}/ValidateOTP?cust_phone=${cust_phone}&OTP=${OTP}`,{'headers' : new HttpHeaders ({'Content-Type' : 'application/json','encodedkey': this._globalservice.encodedKey}), observe:'response'}
  )
  
  }

  resendOTP(customerPhone: number, customerName: string){
    var body ={
      customerPhone,
      customerName
    }
    const headers = { 'Content-Type': 'application/json', 'encodedkey': this._globalservice.encodedKey }

  return  this.http.post(this._globalservice.idineUrl+'/ResendOTP'
  ,body,
  {headers}
  )
}


getCustomerName(cust_phone: number){

  const headers = { 'Content-Type': 'application/json', 'encodedkey': this._globalservice.encodedKey }
    return  this.http.get(`${this._globalservice.idineUrl}/GetCustomerName?cust_phone=${cust_phone}`,{headers}
  )
}
 
getMenuItems(){

  const headers = new HttpHeaders({
    'token': localStorage.getItem('token'),
    'encodedkey':this._globalservice.encodedKey
  })
    return  this.http.get("https://devonlineorders.innosolv-idine.com/api/ContactLessCustomerService/GetMenu", { headers: headers })

}


placeOrder(body){
  const headers = new HttpHeaders({
    'Content-Type': 'application/json',
    'token': localStorage.getItem('token'),
    'encodedkey':this._globalservice.encodedKey
  })
return  this.http.post(this._globalservice.idineUrl+'/PlaceOrder'
,body,
{headers}
)
}

modifyOrder(body){
  const headers = new HttpHeaders({
    'Content-Type': 'application/json',
    'token': localStorage.getItem('token'),
    'encodedkey':this._globalservice.encodedKey
  })
return  this.http.post(this._globalservice.idineUrl+'/ModifyOrder'
,body,
{headers}
)
}

requestBill(OrderNumber){
  const headers = new HttpHeaders({
    'Content-Type': 'application/json',
    'token': localStorage.getItem('token'),
    'encodedkey':this._globalservice.encodedKey
  })

  return  this.http.post(this._globalservice.idineUrl+'/RequestBill'
,{OrderNumber},
{headers}
)
}

updatePaymentInfo(){
	const headers = new HttpHeaders({
		'Content-Type': 'application/json',
		'token': localStorage.getItem('token'),
		'encodedkey':this._globalservice.encodedKey
	})
	let payLoad = {};
	if(localStorage.getItem('billDetails')){
		let details = JSON.parse(localStorage.getItem('billDetails'));
		payLoad = {
		   "razorpay_payment_id": details.razorpay_payment_id,
		   "razorpay_order_id": details.razorpay_order_id,
		   "razorpay_signature": details.razorpay_signature,
		   "status":"1",
		   "SBillNumber": details.SBillNumber
		}
	}
	return  this.http.post(this._globalservice.idineUrl+'/SettleBill',payLoad,{headers})
}

}


