import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { OtpVerifyComponent } from './otp-verify/otp-verify.component';
import { FeedbackComponent } from './feedback/feedback.component';
import { EndingComponent } from './ending/ending.component';

const routes: Routes = [
  {path:"",component:LoginComponent},
  {path:"verify",component:OtpVerifyComponent},
  {path:"feedback",component:FeedbackComponent},
  {path:"ending",component:EndingComponent},

  { path: 'menu', loadChildren: () => import('./menu/menu.module').then(m => m.MenuModule) }, { path: 'orders', loadChildren: () => import('./orders/orders.module').then(m => m.OrdersModule) }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
