import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { UserService } from "../service/user.service";
import { GlobalService } from "../service/global.service";
import Swal from "sweetalert2";
@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
})
export class LoginComponent implements OnInit {
  customerPhone: number;
  customerName: string;
  data: any = [];
  loading: boolean;

  constructor(
    private router: Router,
    private _userService: UserService,
    private _globalService: GlobalService
  ) {}

  ngOnInit() {
    if (localStorage.getItem("token")) {
      return this.router.navigate(["/menu"], {
        queryParamsHandling: "preserve",
      });
    }
  }

  getOtp() {
    if (!this.customerPhone || !this.customerName) {
      return Swal.fire("Oops...", "Please enter all fields !", "error");
    }
    if (!this._globalService.encodedKey) {
      return Swal.fire(
        "Oops...",
        "Encodedkey is missing. Please check the url",
        "error"
      );
    }
    this.loading = true;
    this._userService.getOTP(this.customerPhone, this.customerName).subscribe({
      next: (data) => {
        this.data = data;
        if (this.data.info === "success") {
          localStorage.setItem("customerPhone", this.customerPhone.toString());
          localStorage.setItem("customerName", this.customerName);
          this._globalService.customerPhone = this.customerPhone;
          this._globalService.loginStatus = true;
          this.loading = false;
          this.router.navigate(["./verify"], {
            queryParamsHandling: "preserve",
          });
        }
      },
      error: (error) => {
        this.loading = false;
        if (error.status == 403) {
          localStorage.clear();
          sessionStorage.clear();
          Swal.fire(
            "Oops...",
            "Session Expired ! Please login again.",
            "error"
          );
          this.router.navigate(["/"], { queryParamsHandling: "preserve" });
        } else {
          console.log(error.error);

          Swal.fire("Oops...", `${error.error.info}`, "error");
        }
      },
    });
  }
}
